
<template>
  <div class="infocard">
    <div class="information">
      <!-- <h2>{{ mdata }}</h2> -->
      <br>
      <h2></h2>
      <br>
      <p class="date2">
        App
      </p>
      <p class="date2">
        APIUATJavaPython(
        Selenium)testNGPytestassertkatalonE2EAPI
      </p>

      <!-- <p class="date2">
         JavaSeleniumtestNG android  ios bitbucketjenkinssource code
        udemyside-project
      </p> -->
    </div>
    <div class="photography"><img src="../assets/title.png" alt="" /></div>
  </div>
</template>

<script>
export default {
  name: "Introduce",
  // props: {
  //   mdata: {},
  // },
  inject:['reload'],
  data() {
    return {
      backupURL: "@/assets/bg.png",
    };
  },
  methods: {
    reflesh(){
      this.reload()
    },
  }
};
</script>

<style scoped>
.infocard {
  background: var(--background-shade-color);
  display: flex;
  margin: 0 auto;
  margin-top: 30px;
  margin-bottom: 30px;
  padding: 15px;
  box-shadow: 3px 3px 6px var(--blackdrop-color);
  border-radius: 7px;
  min-height: 150px;
  max-width: 650px;
}
.information {
  background: url("../assets/bg.png") 0 0 no-repeat;
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
}

h3,
p {
  margin: 5px 0;
  margin-right: 10px;
}
h2 {
  color: black;
}
h3 {
  font-size: 1em;
  color: black;
}
p.date {
  font-size: 0.9em;
  color: black;
}
p.date2 {
  font-size: 0.9em;
  color: black;
  text-align: left;
}
a {
  text-decoration: none;
  color: var(--black-color);
}
a:hover {
  color: var(--blackdrop-color);
}
a:hover span.spaceHover::before {
  content: "_";
  color: var(--background-shade-color);
}
.information p.link {
  padding-top: 20px;
}

@media (max-width: 800px) {
  .infocard {
    flex-direction: column-reverse;
  }
  .photography {
    text-align: center;
  }
  .photography img {
    max-width: 100%;
    height: auto;
  }
}
</style>



