<template>
  <div>
    <div
      id="pagetop"
      class="fixed right-0 bottom-0"
      v-show="scY > 300"
      @click="toTop"
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="48"
        height="48"
        viewBox="0 0 24 24"
        fill="none"
        stroke="#f9f6de"
        stroke-width="1"
        stroke-linecap="square"
        stroke-linejoin="arcs"
      >
        <path d="M18 15l-6-6-6 6" />
      </svg>
    </div>
    <div v-for="(item, i) of product" :key="i">
      <div class="infocard">
        <div class="information">
          <h2>{{ item.name }}</h2>
          <p class="date">{{ item.job }}</p>
          <p class="date">{{ item.age }}</p>
          <p class="date2"></p>
          <p class="date2">{{ item.Dec }}</p>
          <p class="date2">{{ item.Dec1 }}</p>
          <p class="date2">{{ item.Dec2 }}</p>
          <p class="date2">{{ item.Dec3 }}</p>
          <p class="date2">{{ item.Dec4 }}</p>
          <p class="date2">{{ item.Dec5 }}</p>
          <p class="date2">{{ item.Dec6}}</p>
          <p class="date2">{{ item.Dec7 }}</p>
          <!-- <p class="link">
            <a v-if="'url' in item == true" target="_blank" :href="item.url">
              
              <span class="spaceHover">&nbsp;</span>
              >&nbsp; 
            </a>
            <a v-else>
              
              <span class="spaceHover">&nbsp;</span>
              >&nbsp; </a>
          </p> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "InfoCard",
  inject:['reload'],
  // props: {
  //   mdata: {},
  // },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  data() {
    return {
      scTimer: 0,
      scY: 0,
      backupURL: "@/assets/bg.png",
      product: [
      {
          id: "0",
          name: "",
          age: "8",
          job: "",
          // Dec: "1.2.3.4.pythonseleniumipytest",
          Dec:"1.story",
          Dec1:"2.Javaseleniumappium(iOS  Android",
          Dec2:"3.eclipse testNG",
          Dec3:"4.bitbucket",
          Dec4:"5.jenkinsbitbucket",
          Dec5:"6.Demo",
        },
        {
          id: "1",
          name: "",
          age: "18",
          job: "",
          // Dec: "1.2.3.4.pythonseleniumpytest",
          Dec:"1.",
          Dec2:"2.",
          Dec3:"3.",
          Dec4:"4.pythonseleniumpytest",
          Dec5:"5.JavaSikulixTestNGend to end",
          Dec6:"6.postmanrequestapi",
          Dec7:"7.UserTest plan",
        },
        {
          id: "2",
          name: "cubepayment()",
          age: "17",
          job: "",
          Dec: "1.",
          Dec2: "2.Apiwith postman insomnia",
          Dec3: "3.Katalon",
          Dec4: "4.Utrack",
          Dec5: "5.local enviroment",
          Dec6: "6.userpythonselenium",
          Dec7: "7.Linux log",
        },
        {
          id: "3",
          name: "",
          age: "5",
          job: "",
          Dec: "1.Sap Hybris ",
          Dec2: "2.Uml",
          Dec3: "3.Product Specifications",
          Dec4: "4.",
        },
        {
          id: "4",
          name: "",
          age: "2",
          job: "",
          Dec: "1.",
          Dec2: "2.",
          Dec3: "3.",

          
        },
      ],
    };
  },
  methods: {
    reflesh(){
      this.reload()
    },
    handleScroll: function () {
      if (this.scTimer) return;
      this.scTimer = setTimeout(() => {
        this.scY = window.scrollY;
        clearTimeout(this.scTimer);
        this.scTimer = 0;
      }, 100);
    },
    toTop: function () {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
};
</script>

<style scoped>
.infocard {
  background: var(--background-shade-color);
  display: flex;
  margin: 0 auto;
  margin-top: 30px;
  padding: 15px;
  box-shadow: 3px 3px 6px var(--blackdrop-color);
  border-radius: 7px;
  min-height: 150px;
  max-width: 650px;
}
.information {
  background: url("../assets/bg.png") 0 0 no-repeat;
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
}
.photography {
  text-align: right;
  vertical-align: middle;
  align-self: right;
  flex-basis: 40%;
  padding: 30px 5px;
}
.photography img {
  border-radius: 7px;
  right: 0px;
  max-width: 250px;
  height: auto;
}
h3,
p {
  margin: 2px 0;
  margin-right: 10px;
}
h2 {
  color: black;
}
h3 {
  font-size: 1em;
  color: black;
}
p.date {
  font-size: 0.9em;
  color: black;
}
p.date2 {
  font-size: 0.9em;
  color: black;
  text-align: left;
}
a {
  text-decoration: none;
  color: var(--black-color);
}
a:hover {
  color: var(--blackdrop-color);
}
a:hover span.spaceHover::before {
  content: "_";
  color: var(--background-shade-color);
}
.information p.link {
  padding-top: 20px;
}

@media (max-width: 800px) {
  .infocard {
    flex-direction: column-reverse;
  }
  .photography {
    text-align: center;
  }
  .photography img {
    max-width: 100%;
    height: auto;
  }
}
</style>



